/* -------------------Hero section----------------------- */
.heroSection{
    /* background-color: #662c7a; */
    background: linear-gradient(-45deg, #e23d72, #7b1fa2);
    height: 95vh;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    color: white;
    font-size: 2rem;
    font-weight: 500;
    overflow: hidden;
}
.imgContainer{
    width: 60%;
    background-image: url(/P2P-Lending/static/media/Landing2.63e341a3.png); 
    height: 100%;
    z-index: 2;
    background-position: right;
    background-repeat: no-repeat;
    background-size: contain; 
}

.imgTopContent{
    width: 40%;
    height: 100%;
    display: grid;
    place-items: center;
    /* background-color: #662c7a; */
    padding: 0 25px;
}

.heroTitle{
    font-size: 4rem;
    font-weight: 700;
    color: #fed25d;
}

.heroText{
    font-size: 1.2rem;
    line-height: 1.4;
    color: white;
}


@media screen and (max-width: 1064px) {
    .heroTitle{
        font-size: 3.5rem;
    }
    .heroText{
        font-size: 1.1rem;
        line-height: 1.3;
    }
}

@media screen and (max-width: 946px) {
    .heroTitle{
        font-size: 3.2rem;
    }
}

@media screen and (max-width: 576px) {
    .heroSection{
        height: 95vh;
        flex-direction: column-reverse;
    }
    .imgContainer{
        width: 100%;
        background-size: cover;
        background-position: center;
    }
    .imgTopContent{
        width: 70%;
        -webkit-transform: translateY(-15%);
                transform: translateY(-15%);
    }
    .heroTitle{
        font-size: 3rem;
    }
    .heroText{
        font-size: 1rem;
    }
}


@media screen and (max-width: 476px) {
    .imgTopContent{
        width: 80%;
    }
}
@media screen and (max-width: 450px) {
    .imgContainer{
        background-size : contain;
    }
    .imgTopContent{
        width: 100%;
    }
}

